.header-color {
	background: #c31432; /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #240b36, #c31432); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#240b36,
		#c31432
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
	background: #c31432; /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #240b36, #c31432); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#240b36,
		#c31432
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	text-align: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.background {
	background: #c31432; /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #240b36, #c31432); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#240b36,
		#c31432
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	position: fixed;
	top: 5;
	left: 0;
	width: 100%;
	height: 100%;
}

.social-links {
	display: flex;
	justify-content: space-between;
	width: 20%;
	margin: auto;
	padding-bottom: 5px;
}

.social-links a {
	color: white;
	font-size: 4em;
	padding: 2px;
}

.profile-img {
	height: 350px;
	padding-top: 4em;
}
.projects-grid {
	display: flex;
}

.banner-text {
	background-color: black;
	opacity: 0.8;
	width: 50%;
	margin: auto;
	border-radius: 30px;
}

.banner-text h1 {
	font-size: 66px;
	font-weight: bold;
	color: white;
}

.banner-text hr {
	border-top: 5px dotted white;
	width: 70%;
	margin: auto;
}

.banner-text p {
	color: white;
	font-size: 15px;
	padding: 1em;
}
